.block-statistics {
  width: 100%;
  height: fit-content;
  display: flex;
}
.block-statistics__inner {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1441px) {
  .block-statistics__inner {
    flex-direction: row;
  }
}
.block-statistics__inner .col {
  width: 100%;
  padding: 60px 20px;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(var(--primary-color));
}
@media (min-width: 768px) {
  .block-statistics__inner .col {
    padding: 60px;
  }
}
@media (min-width: 1441px) {
  .block-statistics__inner .col {
    height: 100%;
  }
}
.block-statistics__inner .col__inner {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.block-statistics__inner .col .pretitle {
  margin-bottom: 15px;
  line-height: 20px;
  font-weight: 500;
}
@media (min-width: 768px) {
  .block-statistics__inner .col .pretitle {
    font-size: 16px;
  }
}
.block-statistics__inner .col .content p,
.block-statistics__inner .col .content span,
.block-statistics__inner .col .content li {
  color: #fff;
}
.block-statistics__inner .col .content a {
  color: rgb(var(--secondary-color));
}
.block-statistics__inner .col .content a:hover {
  color: rgb(var(--primary-color));
}
.block-statistics__inner .col .content p {
  margin-bottom: 15px;
}
.block-statistics__inner .col .content li {
  position: relative;
  margin-bottom: 15px;
  padding-left: 20px;
  display: block;
}
.block-statistics__inner .col .content li::before {
  content: url("./square-check-regular.svg");
  width: 14px;
  height: 14px;
  position: absolute;
  left: 0;
  top: 2px;
  display: block;
}
.block-statistics__inner .col:first-child .block-title {
  margin-bottom: 40px;
  color: #fff;
}
.block-statistics__inner .col:first-child .block-title strong,
.block-statistics__inner .col:first-child .block-title span {
  color: rgb(var(--secondary-color));
}
.block-statistics__inner .col:first-child .link {
  display: flex;
  align-items: center;
  font-family: var(--poppins);
  color: rgb(var(--secondary-color));
  transition: 0.3s linear;
}
.block-statistics__inner .col:first-child .link span {
  margin-left: 6px;
  font-size: 12px;
  line-height: 30px;
  font-weight: 600;
}
.block-statistics__inner .col:first-child .link:hover {
  color: #fff;
}
.block-statistics__inner .col:nth-child(2) {
  background-color: rgb(var(--secondary-color));
}
.block-statistics__inner .col:nth-child(2) .pretitle {
  color: rgb(var(--primary-color));
}
.block-statistics__inner .col:nth-child(2) .title {
  margin-bottom: 30px;
  color: #fff;
}
.block-statistics__inner .col:nth-child(2) .graphics {
  width: 100%;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.block-statistics__inner .col:nth-child(2) .graphics .graph {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 10px;
}
.block-statistics__inner .col:nth-child(2) .graphics .graph span {
  width: fit-content;
  flex: 0;
  display: flex;
  font-family: var(--roboto);
  font-size: 16px;
  font-weight: 400;
  white-space: nowrap;
  color: #fff;
}
.block-statistics__inner .col:nth-child(2) .graphics .graph .bar {
  height: 10px;
  flex: 0 0 100%;
  display: flex;
  background-color: #fff;
}
.block-statistics__inner .col:nth-child(2) .graphics .graph .bar hr {
  width: 0;
  height: 100%;
  display: block;
  background-color: rgb(var(--primary-color));
}
.block-statistics__inner .col:last-child .pretitle {
  color: #fff;
}
.block-statistics__inner .col:last-child .title {
  margin-bottom: 30px;
  color: rgb(var(--secondary-color));
}
.block-statistics__inner .col:last-child .amounts {
  padding: 20px 40px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (min-width: 768px) {
  .block-statistics__inner .col:last-child .amounts {
    padding: 20px 0;
    flex-direction: row;
    justify-content: space-between;
  }
}
.block-statistics__inner .col:last-child .amounts .amount {
  display: flex;
  flex-direction: column;
}
.block-statistics__inner .col:last-child .amounts .amount .value {
  margin-bottom: 10px;
  font-family: var(--poppins);
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  color: #fff;
}
.block-statistics__inner .col:last-child .amounts .amount .concept {
  margin-bottom: 5px;
  font-size: 25px;
  font-weight: 300;
  text-transform: capitalize;
  color: #fff;
}